import { useEffect, React, useRef } from 'react';
import ScrollReveal from "scrollreveal";
import { useNavigate } from 'react-router-dom';
import {
  Box,
  Card,
  CardContent,
  Typography,
  Button,
  Grid,
  Chip
} from '@mui/material';
import HowToVoteIcon from '@mui/icons-material/HowToVote';

// import "../CSS/upcomingElections.css" // Removing custom CSS

const UpcomingElections = ({ voteStatus }) => {
  const navigate = useNavigate();

  const revealRefBottom = useRef(null);
  const revealRefLeft = useRef(null);
  const revealRefTop = useRef(null);

  useEffect(() => {
    const sr = ScrollReveal({
      duration: 800,
      delay: 100,
      distance: '30px',
      easing: 'ease',
      reset: false
    });

    if (revealRefBottom.current) sr.reveal(revealRefBottom.current, { origin: 'bottom' });
    if (revealRefLeft.current) sr.reveal(revealRefLeft.current, { origin: 'left' });
    if (revealRefTop.current) sr.reveal(revealRefTop.current, { origin: 'top' });
  }, []);

  return (
    <Box sx={{ mt: 4, mb: 4 }} className="upcomingElections">
      <Typography variant="h4" component="h2" align="center" gutterBottom sx={{
        color: 'var(--text-primary)',
        mb: 5,
        fontWeight: 'var(--font-weight-extrabold)',
        fontFamily: 'var(--font-primary)',
        letterSpacing: '-0.025em'
      }} ref={revealRefTop}>
        Upcoming Elections
      </Typography>

      <Grid container justifyContent="center" ref={revealRefLeft}>
        <Grid item xs={12} md={8} lg={7}>
          <Card
            className="card-standard"
            sx={{
              borderRadius: 'var(--radius-xl)',
              overflow: 'hidden',
              background: 'var(--grad-surface)',
              border: '1px solid var(--border-color)',
              position: 'relative',
              '&::before': {
                content: '""',
                position: 'absolute',
                top: 0,
                left: 0,
                width: '100%',
                height: '6px',
                background: 'var(--grad-primary)'
              }
            }}
          >
            <CardContent sx={{ textAlign: 'center', p: { xs: 3, md: 5 } }}>
              <Box sx={{ display: 'inline-flex', mb: 2 }}>
                <Chip
                  label="Active Election"
                  className={!voteStatus ? "pulse-primary" : ""}
                  sx={{
                    backgroundColor: 'var(--color-primary)',
                    color: 'white',
                    fontWeight: 'var(--font-weight-bold)',
                    fontSize: '0.75rem',
                    height: 24
                  }}
                />
              </Box>

              <Typography variant="h4" component="h3" gutterBottom sx={{
                fontWeight: 'var(--font-weight-bold)',
                color: 'var(--color-primary)',
                fontFamily: 'var(--font-primary)'
              }}>
                WCU Student President Election
              </Typography>

              <Typography variant="body1" paragraph sx={{
                color: 'var(--text-secondary)',
                mb: 4,
                fontSize: '1.1rem',
                lineHeight: 1.6,
                maxWidth: '600px',
                mx: 'auto'
              }}>
                Vote to elect the Wachemo University Student President. Your participation shapes the future of our campus community.
              </Typography>

              <Button
                variant="contained"
                size="large"
                startIcon={<HowToVoteIcon />}
                onClick={() => navigate('/Vote')}
                disabled={voteStatus}
                sx={{
                  px: 6,
                  py: 2,
                  borderRadius: 'var(--radius-full)',
                  textTransform: 'none',
                  fontSize: '1.1rem',
                  fontWeight: 'var(--font-weight-bold)',
                  background: 'var(--grad-primary)',
                  boxShadow: '0 8px 32px var(--button-shadow)',
                  '&:hover': {
                    background: 'var(--color-primary-dark)',
                    transform: 'translateY(-2px)'
                  },
                  '&:disabled': {
                    backgroundColor: 'var(--bg-secondary)',
                    color: 'var(--text-muted)'
                  },
                  transition: 'all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275)'
                }}
              >
                {voteStatus ? "Submission Received" : "Cast Your Vote Now"}
              </Button>

              <Box sx={{ mt: 3 }}>
                <Typography variant="caption" sx={{ color: 'var(--text-muted)', display: 'block' }}>
                  Secure • Encrypted • Confidential
                </Typography>
              </Box>
            </CardContent>
          </Card>
        </Grid>
      </Grid>
    </Box>
  )
}
export default UpcomingElections;