import * as React from 'react';
import { useState, useRef } from 'react';
import { BASE_URL } from '../../../../helper';
import axios from 'axios';
import Cookies from 'js-cookie';
import {
    Card,
    CardContent,
    Typography,
    Avatar,
    Box,
    Chip,
    CircularProgress,
    IconButton,
    Tooltip
} from '@mui/material';
import PhotoCamera from '@mui/icons-material/PhotoCamera';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import CancelIcon from '@mui/icons-material/Cancel';

// Default avatar image for users without a profile photo
const DEFAULT_AVATAR = 'data:image/svg+xml;base64,';

export default function UserCard({ voter, onPhotoUpdate }) {
    const [uploading, setUploading] = useState(false);
    const [currentPhotoUrl, setCurrentPhotoUrl] = useState(voter.img || voter.image);
    const fileInputRef = useRef(null);

    // Get the profile photo URL - check both 'img' and 'image' fields
    const profilePhotoUrl = currentPhotoUrl || voter.img || voter.image;
    const fullPhotoUrl = profilePhotoUrl ? `${BASE_URL}${profilePhotoUrl}` : DEFAULT_AVATAR;

    const handlePhotoClick = () => {
        fileInputRef.current.click();
    };

    const handlePhotoUpload = async (event) => {
        const file = event.target.files[0];
        if (!file) return;

        // Validate file type
        const validTypes = ['image/jpeg', 'image/jpg', 'image/png', 'image/gif', 'image/webp'];
        if (!validTypes.includes(file.type)) {
            alert('Please select a valid image file (JPEG, PNG, GIF, or WebP)');
            return;
        }

        // Validate file size (max 5MB)
        if (file.size > 5 * 1024 * 1024) {
            alert('Image size should be less than 5MB');
            return;
        }

        setUploading(true);

        try {
            const voterId = voter._id || Cookies.get('myCookie');
            if (!voterId) {
                alert('Session expired. Please login again.');
                return;
            }

            const formData = new FormData();
            formData.append('photo', file);

            const response = await axios.post(
                `${BASE_URL}/uploadVoterPhoto/${voterId}`,
                formData,
                {
                    headers: {
                        'Content-Type': 'multipart/form-data',
                    },
                }
            );

            if (response.data.success) {
                setCurrentPhotoUrl(response.data.img);
                alert('Profile photo updated successfully!');
                // Call parent callback if provided
                if (onPhotoUpdate) {
                    onPhotoUpdate(response.data.img);
                }
                // Reload page to show updated photo everywhere
                window.location.reload();
            } else {
                alert(response.data.message || 'Failed to upload photo');
            }
        } catch (error) {
            console.error('Error uploading photo:', error);
            alert('Failed to upload photo. Please try again.');
        } finally {
            setUploading(false);
        }
    };

    return (
        <Card
            className="card-standard"
            sx={{
                borderRadius: 'var(--radius-xl)',
                height: '100%',
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                p: 3,
                backgroundColor: 'var(--card-bg)',
                border: '1px solid var(--border-color)',
                boxShadow: 'var(--shadow-md)',
                position: 'relative',
                overflow: 'visible'
            }}
        >
            <Box sx={{
                position: 'relative',
                mt: 1,
                mb: 3,
                '&::after': {
                    content: '""',
                    position: 'absolute',
                    top: -8,
                    left: -8,
                    right: -8,
                    bottom: -8,
                    borderRadius: '50%',
                    background: 'var(--grad-primary)',
                    zIndex: -1,
                    opacity: 0.2
                }
            }}>
                <Avatar
                    src={fullPhotoUrl}
                    alt={voter.name || 'User Profile'}
                    sx={{
                        width: 160,
                        height: 160,
                        border: '4px solid var(--bg-surface)',
                        boxShadow: '0 8px 32px var(--shadow-color)',
                        transition: 'transform 0.3s ease'
                    }}
                    imgProps={{ onError: (e) => { e.target.src = DEFAULT_AVATAR; } }}
                />
                <Tooltip title="Change Photo">
                    <IconButton
                        onClick={handlePhotoClick}
                        sx={{
                            position: 'absolute',
                            bottom: 8,
                            right: 8,
                            backgroundColor: 'var(--color-primary)',
                            color: 'white',
                            width: 40,
                            height: 40,
                            boxShadow: '0 4px 12px var(--button-shadow)',
                            '&:hover': {
                                backgroundColor: 'var(--color-primary-dark)',
                                transform: 'scale(1.1)'
                            },
                            transition: 'all 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275)'
                        }}
                        disabled={uploading}
                    >
                        {uploading ? <CircularProgress size={20} color="inherit" /> : <PhotoCamera sx={{ fontSize: 20 }} />}
                    </IconButton>
                </Tooltip>

            </Box>

            <input
                type="file"
                ref={fileInputRef}
                onChange={handlePhotoUpload}
                accept="image/*"
                style={{ display: 'none' }}
            />

            <CardContent sx={{ width: '100%', textAlign: 'center', p: 0 }}>
                <Typography variant="h5" component="div" sx={{
                    fontWeight: 'var(--font-weight-bold)',
                    color: 'var(--text-primary)',
                    fontFamily: 'var(--font-primary)',
                    mb: 2
                }}>
                    {voter.name || `${voter.firstName || ''} ${voter.lastName || ''}`}
                </Typography>

                <Box sx={{
                    mb: 3,
                    p: 2,
                    borderRadius: 'var(--radius-lg)',
                    backgroundColor: 'rgba(104, 112, 250, 0.05)',
                    border: '1px dashed var(--border-color)'
                }}>
                    <Typography variant="body2" color="var(--text-secondary)" sx={{
                        textTransform: 'uppercase',
                        letterSpacing: 2,
                        fontSize: '0.7rem',
                        fontWeight: 'var(--font-weight-bold)',
                        mb: 0.5
                    }}>
                        Student ID
                    </Typography>
                    <Typography variant="h6" sx={{
                        color: 'var(--color-primary)',
                        fontWeight: 'var(--font-weight-extrabold)',
                        fontFamily: 'var(--font-mono)'
                    }}>
                        {voter.voterId || voter.voterid || '-'}
                    </Typography>
                </Box>

                <Box sx={{ mt: 1 }}>
                    <Chip
                        icon={voter.voteStatus ? <CheckCircleIcon style={{ color: 'white' }} /> : <CancelIcon style={{ color: 'white' }} />}
                        label={voter.voteStatus ? "Vote Casted" : "Voting Pending"}
                        sx={{
                            fontWeight: 'var(--font-weight-bold)',
                            px: 2,
                            py: 2.5,
                            borderRadius: 'var(--radius-md)',
                            backgroundColor: voter.voteStatus ? 'var(--color-success)' : 'var(--color-error)',
                            color: 'white',
                            fontSize: '0.9rem',
                            boxShadow: voter.voteStatus
                                ? '0 4px 12px rgba(76, 206, 172, 0.3)'
                                : '0 4px 12px rgba(219, 79, 74, 0.3)'
                        }}
                    />
                </Box>
            </CardContent>
        </Card>
    );
}
